const initialState = {
    check: false
  }
 export const check = (state = {check: false}, action) => {
      switch (action.type){
          case 'CHECK_BOX':
          console.warn("action done")
              return [
                  ...state,
              {
                check : !check,
                id: action.id,
                text: action.text,
      
              }    
            ]        
          default:
              return state
      }
  }

  export default check